import React from "react";
import {
  Divider,
  List,
  ListItem,
  Typography,
} from "@mui/material";
import { getGamesStats, getMeetingsStats, getPlayersStats } from "../games/statsSelectors";
import StatItem from "./Common/StatItem";
import { getFromNow } from "../utils/date";
import { calcPercent } from "../utils/utils";

const gamesLimit = 10;
const meetings = getMeetingsStats();
const games = getGamesStats();
const players = getPlayersStats(gamesLimit);

export default function Stats() {

  return (
    <div className="stats">
      <List>
        <ListItem><Typography variant="h6"></Typography></ListItem>
        <Divider />
        <StatItem label="" value={meetings.count} />
        <StatItem label="" value={getFromNow(meetings.lastDate)} />
      </List>
      <List>
        <ListItem><Typography variant="h6"></Typography></ListItem>
        <Divider />
        <StatItem label="" value={games.count} />
        <StatItem label=" " value={`${games.mafiaWin} ${calcPercent(games.count, games.mafiaWin)}`} />
        <StatItem label=" " value={`${games.cityWin} ${calcPercent(games.count, games.cityWin)}`} />
      </List>
      <List>
        <ListItem><Typography variant="h6"> ( {gamesLimit}  )</Typography></ListItem>
        <Divider />
        <StatItem label="" value={players.count} />
        <StatItem label="" value={players.maxRating.name} />
      </List>
      <List>
        <StatItem label=" " value={players.maxGames.name} />
        <StatItem label=" " value={players.minGames.name} />
      </List>
      <List>
        <StatItem label=" " value={players.maxCitizen.name} />
        <StatItem label=" " value={players.maxMob.name} />
        <StatItem label=" " value={players.maxSheriff.name} />
        <StatItem label=" " value={players.maxDon.name} />
      </List>
    </div>
  )
}